import React, {useEffect, useState, useRef} from 'react'
import {draw, setup, useDeviceDetect} from '../../../../Helpers'
import { ImageWrapper, ProjectMainSection } from '../../../SectionsComponents'
import dynamic from 'next/dynamic'

const Sketch = dynamic(() => import('react-p5'), { ssr: false })
/*IMAGES*/
const MainStatue = '/images/Manticore/MainStatue.png'

const opt = {
  statueW: 610,
  statueH: 840,
  statue: MainStatue,
  posX: 'right',
  posY: 'bottom',
  proportions: 'origin',
  offsetX: 0,
  offsetY: 0,
  bg: '#fff'
}

const optMobile = {
  statueW: 350,
  statueH: 480,
  statue: MainStatue,
  posX: 'right',
  posY: 'bottom',
  proportions: 'origin',
  offsetX: 0,
  offsetY: 0,
  bg: '#fff'
}


const MainSectionFooterContent = () => {
  const { minWidthLaptop } = useDeviceDetect()
  const [scrolled, setScrolled] = useState(true)
  const [lastHeight, setLastHeight] = useState(0);
  const wrapper = useRef(null);

  const handleScroll = () => {
    if(wrapper.current) {
      const elem = wrapper.current;
      setLastHeight(elem.getBoundingClientRect().height + 50);
      console.log(window.scrollY, elem.getBoundingClientRect().height + 50, lastHeight)
      if(window.scrollY > elem.getBoundingClientRect().height + 50) {
        setScrolled(false)
      } else if(window.scrollY <= lastHeight) {
        setScrolled(true)
      }
    }
  }

  useEffect(() => {
    document.addEventListener('scroll', handleScroll)
  }, [])

  return (
      <div ref={wrapper} className="section-image-wrap main-statue">
        {scrolled ? <Sketch setup={minWidthLaptop ? setup(opt) : setup(optMobile)} draw={draw}/> : ''}
      </div>
  )
}

export const Main = () => {
  return (
    <ProjectMainSection id="manticore" name="Manticore" content={MainSectionFooterContent} isContentWhite/>
  )
}
